<script>
import { MailFilled, CheckCircleFilled, WarningFilled } from '@ant-design/icons-vue'
import Card from '@/components/card.vue'
import * as echarts from 'echarts'
export default {
  name: 'homeIndex',
  components: {
    Card,
    MailFilled, CheckCircleFilled, WarningFilled
  },
  data () {
    return {
      filterBy: 'total',
      filterByRadioData: {
        total: 100,
        release: 66.6,
        intercept: 33.3
      },

      attackBy: 'total',
      attackByRadioData: {
      },

      series_user: [999, 999, 999],
      chartOptions_user: {
        chart: {
          type: 'donut'
        },
        labels: ['正常用户', '高危人群', '异常发件人'],
        plotOptions: {
          pie: {
            customScale: 0.8,
            dataLabels: {
              offset: 40,
            },
            expandOnClick: false
          }
        },
        dataLabels: {
          formatter (val, opts) {
            const name = opts.w.globals.labels[opts.seriesIndex]
            return [name, val.toFixed(1) + '%']
          },
        },
      },

      dataDistribution_pie: null,
      series_data: [33, 66],
      chartOptions_data: {
        chart: {
          type: 'donut'
        },
        labels: ['拦截邮件', '放行邮件'],
        colors: ['rgb(242, 65, 95)', 'rgb(0, 227, 150)'],
        plotOptions: {
          pie: {
            customScale: 0.8,
            dataLabels: {
              offset: 40,
            },
            expandOnClick: false
          }
        },
        dataLabels: {
          formatter (val, opts) {
            const name = opts.w.globals.labels[opts.seriesIndex]
            return [name, val.toFixed(1) + '%']
          },
        },
      },

      series_attack: [{
        name: '拦截数',
        data: [29, 82, 45, 32, 74, 39, 41, 23, 52, 35, 50, 42, 30, 40, 93, 61, 47, 42, 61, 34, 75, 57, 16, 59, 64, 72, 41, 49, 54, 2]
      }],
      chartOptions_attack: {
        chart: {
          type: 'area'
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: 'smooth'
        },
        xaxis: {
          labels: {
            style: {
              colors: 'rgba(255,255,255,0.9)'
            }
          }
        },
        yaxis: {
          labels: {
            style: {
              colors: 'rgba(255,255,255,0.9)'
            }
          }
        },
        tooltip: {
          theme: 'dark'
        }
      },

      series_accuracy: [100],
      chartOptions_accuracy: {
        chart: {
          type: 'radialBar',
          toolbar: {
            show: true
          }
        },
        colors: ['rgb(18, 175, 108)'],
        // colors: ['rgb(52,124,175)'],// 圆环原色
        plotOptions: {
          radialBar: {
            startAngle: -135,
            endAngle: 225,
            hollow: {
              margin: 0,
              size: '70%',
              background: '#325691',// 圆心背景
              position: 'front',
              dropShadow: {
                enabled: true,
                top: 3,
                left: 0,
                blur: 4,
                opacity: 0.24
              }
            },
            track: {
              background: 'rgb(3, 25, 63)',// 整体背景
              strokeWidth: '200%',
              margin: 0
            },
            dataLabels: {
              show: true,
              name: {
                offsetY: -3,
                show: true,
                color: '#efefef',
                fontSize: 'calc(0.1em + 0.75vw)'
              },
              value: {
                offsetY: 5,
                formatter: function (val) {
                  return parseInt(val) + '%';
                },
                color: '#efefef',
                fontSize: 'calc(0.1em + 0.7vw)',
                show: true,
              }
            }
          }
        },
        fill: {
          type: 'gradient',
          gradient: {
            shade: 'dark',
            type: 'horizontal',
            shadeIntensity: 0.5,
            gradientToColors: ['#96e7a3'],
            inverseColors: true,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 100]
          }
        },
        stroke: {
          lineCap: 'round'
        },
        labels: ['准确率']
      },

      series_error: [100],
      chartOptions_error: {
        chart: {
          type: 'radialBar',
          toolbar: {
            show: true
          }
        },
        // colors: ['rgb(52,124,175)'],// 圆环原色
        colors: ['#c50021'],
        plotOptions: {
          radialBar: {
            startAngle: -135,
            endAngle: 225,
            hollow: {
              margin: 0,
              size: '70%',
              background: '#325691',// 圆心背景
              position: 'front'
            },
            track: {
              background: 'rgb(3, 25, 63)',
              strokeWidth: '200%',
              margin: 0
            },
            dataLabels: {
              show: true,
              name: {
                offsetY: -3,
                show: true,
                color: '#efefef',
                fontSize: 'calc(0.1em + 0.75vw)'
              },
              value: {
                offsetY: 5,
                formatter: function (val) {
                  return parseInt(val) + '%';
                },
                color: '#efefef',
                fontSize: 'calc(0.1em + 0.7vw)',
                show: true
              }
            }
          }
        },
        fill: {
          type: 'gradient',
          gradient: {
            shade: 'dark',
            type: 'horizontal',
            shadeIntensity: 0.5,
            gradientToColors: ['#f88'],
            inverseColors: true,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 100]
          }
        },
        stroke: {
          lineCap: 'round'
        },
        labels: ['误判率']
      }
    }
  },
  mounted () {
    this.dataDistribution_pie = echarts.init(this.$refs.dataDistribution_pie, null, {
      renderer: 'svg'
    })
    const option_typePreferences_pie = {
      tooltip: {
        trigger: 'item'
      },
      series: [
        {
          type: 'pie',
          radius: '50%',
          data: [
            {
              value: 1048,
              name: '放行邮件',
              itemStyle: {
                color: 'rgb(80,135,236)'
              }
            },
            {
              value: 735,
              name: '拦截邮件',
              itemStyle: {
                color: 'rgb(242,65,95)'
              }
            }
          ],
          label: {
            normal: {
              formatter: '{b}\n{d}%',
              color: '#fff'
            }
          },
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      ]
    }
    this.dataDistribution_pie.setOption(option_typePreferences_pie)
    window.addEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize () {
      if (this.dataDistribution_pie) {
        this.dataDistribution_pie.resize();
      }
    },
    filterByRadioChange () {
      if (this.filterBy === 'total') {
        this.filterByRadioData = {
          total: 100,
          release: 66.6,
          intercept: 33.3
        }
      } else if (this.filterBy === 'year') {
        this.filterByRadioData = {
          total: 90,
          release: 55.5,
          intercept: 22.2
        }
      } else if (this.filterBy === 'season') {
        this.filterByRadioData = {
          total: 80,
          release: 44.4,
          intercept: 11.1
        }
      } else if (this.filterBy === 'month') {
        this.filterByRadioData = {
          total: 70,
          release: 33.3,
          intercept: 10
        }
      } else if (this.filterBy === 'week') {
        this.filterByRadioData = {
          total: 60,
          release: 22.2,
          intercept: 6.6
        }
      } else if (this.filterBy === 'day') {
        this.filterByRadioData = {
          total: 50,
          release: 11.1,
          intercept: 3.3
        }
      }
    },
    attackByRadioChange () {
      if (this.attackBy === 'total') {
        this.series_attack[0].data = [29, 82, 45, 32, 74, 39, 41, 23, 52, 35, 50, 42, 30, 40, 93, 61, 47, 42, 61, 34, 75, 57, 16, 59, 64, 72, 41, 49, 54, 22]
      } else if (this.attackBy === 'year') {
        this.series_attack[0].data = [82, 52, 61, 49, 93, 50, 41, 59, 72, 64, 39, 42, 74, 57, 47, 75, 61, 22, 23, 45, 54, 40, 32, 30, 16, 41, 42, 29, 34, 35]
      } else if (this.attackBy === 'season') {
        this.series_attack[0].data = [61, 29, 23, 16, 59, 50, 35, 52, 47, 61, 45, 82, 42, 64, 39, 30, 74, 41, 32, 22, 41, 57, 54, 40, 93, 49, 72, 34, 75, 42]
      } else if (this.attackBy === 'month') {
        this.series_attack[0].data = [75, 32, 35, 41, 82, 40, 29, 72, 22, 42, 52, 45, 59, 54, 49, 50, 57, 61, 61, 64, 93, 30, 74, 16, 47, 41, 23, 42, 39, 34]
      } else if (this.attackBy === 'week') {
        this.series_attack[0].data = [54, 49, 23, 32, 42, 72, 41, 61, 22, 61, 34, 75, 16, 29, 64, 52, 39, 59, 47, 40, 45, 41, 42, 74, 82, 57, 30, 93, 50, 35]
      }
    }
  }
}
</script>

